:root {
  --ASTEROID: hsl(0, 0%, 75%);
  --MOON: hsl(0, 0%, 41%);
  --PLANET: hsl(116, 53%, 30%);
  --GAS-GIANT: hsl(180, 100%, 50%);
  --STAR: hsl(61, 100%, 50%);
  --WORMHOLE: rgb(255, 255, 255);
  --x-axis: hsl(0, 100%, 25%);
  --y-axis: hsl(100, 100%, 10%);
  --hovered: hsl(0, 100%, 75%);
}

.spacemap {
  &-main {
    z-index: -10;
  }

  &-canvas {
    display: block;
    overflow-x: visible;
    overflow-y: visible;
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: inherit;
  }
}